import React, { Component } from 'react';

import github from '../assets/github.svg';
import linkedin from '../assets/linkedin.svg';
import envelope from '../assets/envelope.svg';

class Header extends Component {
  render() {
    return (
      <header className="header">
        <div className="center">
          <h1>Thomas W Cardenas</h1>
          <h3>
            Hello! I'm Thomas, I'm a Senior at the University of North Carolina at Charlotte. Go Niners! I do work
            currently at school in the Bioinformatics and Genomics Department working in the Sung Lab creating visuals for
            mutations in bacteria DNA. Currently looking for a position in web development or similar field in East Tennessee and Western North Carolina area.

            <br />
            <br />

            I do freelance web development for projects that I am intersted in, challenging, or am excited about. If you
            are interested in hiring me, feel free to contact me from one of my social medias or email listed below.

            <br />

            <br />
            Outside of coding I spend most of my time conducting family history research. I've been doing this for over 11 years.
            I frequently visit libraries, historical societys, and archives often.

            </h3>
          <div className="social-set">
            <div className="social-set">
              <div className="social-container">
                <img src={github} className="socialHomeItem" alt="logo" />
                <img src={linkedin} className="socialHomeItem" alt="logo" />
              </div>
              <p>twcardenas</p>
            </div>

            <div className="social-set">
              <div className="social-container">
                <img src={envelope} className="socialHomeItem" alt="logo" />
              </div>
              <p>twcardenas@outlook.com</p>
            </div>
          </div>
        </div>
      </header>
    );
  }
}

export default Header;
