import React, { Component } from "react";

class Work extends Component {
  render() {
    return (
      <div className="work">
        <h2 className="page-header">Projects</h2>
        <p className="header-desc">I've done a wide range of projects for personal, educational, and professional purposes. Here are a few I've developed/created.</p>

        <div className="work-item">
          <header>
            <h3>Developing Visualization Tools for Contextual Mutation Patterns in Bacteria</h3>
          </header>
          <div className="content">
            <p className="desc">Mutations play a primary role in both evolution and genetic diseases. These mutations are not randomly distributed across
            genomes. In this work we use visualization to show the pattern and distrubtion of mutations in order to understand this process. In this
        study,the we apply Java, JavaScript, R and Python programming skills to develop the data and visualization tools.</p>

            <p className="desc">My visualizations integrate the of data from a graduate student
               in to visualization tools. Goals of this project is to have an app that is ease of use,
              has complete data integration, and should be able to be hosted and viewed by other
          researchers. I also documentation of tools and analysis. Documentation for usage, reasons for choosing techology, methods, etc.</p>

            <p className="desc">
              Part of this project requires also to read journal articles associated with context-dependent mutation a
              patterns and other articles that involve bioinformatics and genomics that are relevent to Dr. Sung’s
          journal club.</p>
          </div>
          <footer>
            <ul className="technology-stack">
              <li>ReactJS
              </li>
              <li>Python
              </li>
              <li>R
              </li>
              <li>Shell Scripts
              </li>
            </ul>
          </footer>
        </div>

        <div className="work-item">
          <header>
            <h3>Alexandria Continued</h3>
          </header>
          <div className="content">
            <p className="desc">Alexandria Continued is a genealogy web application in which users are able to add a wide range of information to people in their family tree.
            This acts as a family tree database and storage of photos, documents, DNA results, etc. </p>
          </div>
          <footer>
            <ul className="technology-stack">
              <li>ReactJS
              </li>
              <li>GraphQL
              </li>
              <li>NodeJS
              </li>
              <li>Express
              </li>
              <li>MongoDB
              </li>
              <li>Sass
              </li>
            </ul>
          </footer>
        </div>

        <div className="work-item">
          <header>
            <h3>Points++</h3>
          </header>
          <div className="content">
            <p className="desc">Couples sometimes like to keep track of cute things each other say, give each other brownie points, record memories, to-do lists, etc. This 
            app helps record, track, and organize that data. Ensuring these precious memories are saved and easily avaliable.</p>
          </div>
          <footer>
            <ul className="technology-stack">
              <li>ReactJS
              </li>
              <li>Firebase (Auth, Firestore, Hosting)
              </li>
            </ul>
          </footer>
        </div>
      </div >
    );
  }
}

export default Work;
